<template>
  <div class="chartBox">
    <div ref='main' class="chart"></div>
  </div>
</template>

<script>
export default {
  name: 'autoSector',
  props: {
    value: {
      type: Object,
      require: true,
    },
  },
  data() {
    return {
      chart: null,
    };
  },
  components: {},
  methods: {
    initChart() {
      const contain = this.$refs.main;
      this.chart = this.$chat.init(contain);
      this.value && this.chart.setOption(this.value);
    },
    resizeChart() {
      if (this.chart) {
        this.chart.dispose();
        this.initChart();
      }
    },
  },
  mounted() {},
  computed: {},
  watch: {
    value: {
      deep: true,
      immediate: true,
      handler(v) {
        this.$nextTick(() => {
          if (this.chart) {
            this.resizeChart();
          } else {
            this.initChart();
          }
        });
      },
    },
  },
};
</script>

<style lang='scss' scoped>
.chartBox {
  width: 100%;
  height: 300px;
}
.chart {
  width: 100%;
  height: 100%;
}
</style>