<template>
  <div class="chartSetting">
    <JsonEditor v-model="result"  mode='code'  :show-btns="false" @json-change="onJsonChange"></JsonEditor>
  </div>
</template>

<script>
import JsonEditor from 'vue-json-editor';
export default {
  name: 'chartSetting',
  props: {
    value: {
      require: true,
    },
  },
  data() {
    return {
      result: {},
    };
  },
  components: { JsonEditor },
  methods: {
    onJsonChange(value) {
      this.result = value;
      this.$emit('update', value);
    },
    jsonUpdate() {
      return this.result;
      this.$emit('update', this.result);
    },
  },

  watch: {
    value: {
      deep: true,
      immediate: true,
      handler(v) {
        this.result = v;
      },
    },
  },
};
</script>

<style lang='scss' >
.chartSetting {
  position: fixed;
  width: 100%;
  top: 0;
  bottom: 0;
  overflow-y: scroll;
}
.jsoneditor-vue {
  position: absolute ;
  height: 100%;
  width: 100%;
}
.jsoneditor-modes{
      display: none;
   }
</style>